<style>
.el-card {
  margin: 0.2rem;
}
</style>
<script>
export default {
  data() {
    return {
      props: ["id"],
      loading: false,
      message: "",
      step: 1,
      employee: {},
      bank_branches: [],
    };
  },
  computed: {
    banks() {
      if (this.$store.state.dictionary.banks != undefined) {
        var banks = this.$store.state.dictionary.banks.filter(function (bank) {
          return bank.category == "BANK";
        });
        return banks;
      }
    },
  },
  mounted() {
    this.$store.dispatch("dictionary/getDepartments");
    this.$store.dispatch("dictionary/getCountries");
    this.$store.dispatch("dictionary/getBanks");
    this.$store.dispatch("company/getCompanyBranches");
    this.$store.dispatch("company/getJobTitles");
    this.$store.dispatch("company/getEmploymentTypes");
    this.getProfile();
  },
  methods: {
    next(validate) {
      this.$refs[validate].validate((valid) => {
        if (valid) {
          this.step++;
        } else {
          return false;
        }
      });
    },
    getProfile() {
      this.$store.dispatch("company/getEmployeeProfile", this.$route.params.id);
      if (this.$store.state.company.employee_profile != undefined) {
        this.employee = this.$store.state.company.employee_profile;
      }
    },
  },
};
</script>